article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button],
input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

img {
  max-width: 100%; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
  background: #fafafa; }

.page {
  max-width: 1200px;
  margin: 40px auto; }
  .page .year-btns {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
    .page .year-btns button {
      flex-basis: 140px;
      margin-bottom: 1rem; }

.user {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 250px 1fr;
  align-items: center;
  justify-items: center;
  background-color: #4c75a3; }
  .user h2 {
    color: white;
    margin-left: 50px; }

.btn {
  border: none;
  border-radius: 2px;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  outline: 0;
  padding: 0 2rem;
  margin-right: 1rem;
  text-transform: uppercase;
  vertical-align: middle;
  color: #fff;
  background-color: #4c75a3;
  text-align: center;
  letter-spacing: .5px;
  transition: .2s ease-out;
  cursor: pointer; }

.btn:hover {
  background-color: #6383a8; }

.btn-login {
  width: 150px;
  justify-self: flex-end;
  margin-right: 50px;
  background-color: #d14b4c; }

.btn-login:hover {
  background-color: #ba3537; }

.logged {
  width: 150px;
  justify-self: flex-end;
  margin-right: 50px;
  background-color: none;
  color: white; }

.photo-grid {
  display: flex;
  flex-wrap: wrap; }

.grid-figure {
  flex-basis: calc(33.333% - 4rem);
  flex-grow: 0;
  flex-shrink: 0;
  margin: 2rem 2rem 2rem 2rem;
  padding: 2rem;
  background: white;
  box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.1); }
  .grid-figure figcaption {
    text-align: center;
    font-size: 25px;
    color: #f33;
    margin-top: 1rem; }

.grid-photo {
  width: calc(100% + 4rem);
  margin-left: -2rem;
  margin-top: -2rem;
  max-width: none; }
